import styled from "styled-components";

export const Wrapper = styled.div`
  text-align: center;
  position: fixed;
  bottom: 0;
  background-color: white;
  z-index: 100;

  i {
    margin: 0 10px;
    padding: 10px;
    width: 80px;
    font-size: 80px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.3s;
    cursor: pointer;

    &:hover {
      transform: scale(1.1);
    }
  }
`;
