import { useContext } from "react";
import styles from "../../Styles/LangButton/LangButton.module.scss";
import { languageContext } from "../../Context/languageContext";
import { Wrapper, ActiveButton, InactiveButton } from "./LangButton.styles";

export const LangButton: React.FC = () => {
  const { language, setLanguage } = useContext(languageContext);

  const switchLanguage = (lan: string) => {
    localStorage.setItem("language", lan);
    setLanguage(lan);
  };

  return (
    <Wrapper>
      {language === "es" ? (
        <div>
          <ActiveButton>es</ActiveButton>
          <InactiveButton onClick={() => switchLanguage("en")}>
            en
          </InactiveButton>
        </div>
      ) : (
        <div>
          <InactiveButton onClick={() => switchLanguage("es")}>
            es
          </InactiveButton>
          <ActiveButton>en</ActiveButton>
        </div>
      )}
    </Wrapper>
  );
};
