import styled from "styled-components";

export const Wrapper = styled.div`
  position: absolute;
  right: 10px;
  top: 10px;
  border-radius: 10px;
  padding: 2px 4px;
  background-color: black;
  border-radius: 30px;
`;

export const InactiveButton = styled.button`
  border: none;
  background: black;
  margin: 5px;
  cursor: pointer;
  color: white;
  transition: all 0.3s;
  border-radius: 50%;
`;

export const ActiveButton = styled.button`
  color: white;
  border-radius: 50%;
  background: blue;
  padding: 1px 2px;
  transition: all 0.3s;
  border: none;
  cursor: pointer;
  margin: 5px;
`;
