import { working } from "../../Assets/working";
import { Container } from "./Working.styles";

type Props = {
  language: string;
};

export const WorkingOn: React.FC<Props> = ({ language }) => {
  return (
    <Container>
      <h3>
        {language === "es"
          ? "Actualmente estoy trabajando en:"
          : "Im Currently Working on"}
      </h3>
      <div>
        <h5>{working.name}</h5>
        <a href={working.url}>github</a>
      </div>
    </Container>
  );
};
