import { Container } from "./Projects.styled";
import Button from "@material-ui/core/Button";

type Props = {
  name: string;
  img: string;
  description: string;
  descripcion: string;
  demo: string;
  github: string;
  language: string;
};

export const Project: React.FC<Props> = ({
  name,
  img,
  description,
  descripcion,
  demo,
  github,
  language,
}) => {
  return (
    <Container image={img}>
      <div></div>
      <h3>{name}</h3>
      <p>{language === "es" ? descripcion : description}</p>
      <Button
        variant="contained"
        color="primary"
        href={demo}
        target="_blank"
        rel="noreferrer"
      >
        {language === "es" ? "Ver" : "Demo"}
      </Button>
      <Button
        variant="contained"
        color="primary"
        href={github}
        target="_blank"
        rel="noreferrer"
      >
        Github
      </Button>
    </Container>
  );
};
