import styled from "styled-components";

export const Container = styled.div`
  width: 350px;
  height: 350px;
  margin: 30px;

  a {
    margin-left: 10px;
  }

  div {
    background-image: url(${(props: any) => props.image});
    height: 200px;
    width: 100%;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
  }
`;
