import { en } from "../../Localization/en";
import { es } from "../../Localization/es";
import { Wrapper } from "./Title.styles";

type Props = {
  language: string;
};

export const Title: React.FC<Props> = ({ language }) => {
  return (
    <Wrapper>
      <h1>{language === "es" ? es.title : en.title}</h1>
    </Wrapper>
  );
};
