import { Project } from "./Project";
import { projects } from "../../Assets/projects";
import { Container, Projects } from "./ProjectsContainer.styled";

type Props = {
  language: string;
};

export const ProjectsContainer: React.FC<Props> = ({ language }) => {
  return (
    <Container>
      <h2>{language === "es" ? "Proyectos" : "Projects"}</h2>
      <Projects>
        {projects.slice(0, 5).map((project, index) => {
          const { name, img, description, descripcion, demo, github } = project;
          return (
            <Project
              key={index}
              name={name}
              img={img}
              descripcion={descripcion}
              description={description}
              demo={demo}
              github={github}
              language={language}
            />
          );
        })}
      </Projects>
    </Container>
  );
};
